/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
/* CSS Reset */
/* system fonts */
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica,
    Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}
/* system fonts */
body {
  font-size: 12x;
  max-width: 100vw;
}
header {
  background-color: cornflowerblue;
  padding: 1rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
header h1 {
  font-size: 2rem;
}
button {
  background-color: cadetblue;
  border: 2px solid #ffffff;
  border-radius: 5px;
  color: #ffffff;
  padding: 0.5rem 1rem;
}
.navigation {
  position: relative;
}
.menu {
  position: absolute;
  left: -2rem;
}
.nav-links {
  background-color: #ffffff;
  padding: 0.5rem 2rem;
  margin-top: 1rem;
  border-radius: 5px;
}
.hide {
  display: none;
}
.nav-link {
  padding: 0.5rem 0;
}
footer {
  background-color: cornflowerblue;
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-items: center;
  padding: 1rem 2rem;
  position: sticky;
  bottom: 0;
  left: 0;
  right: 0;
}
footer .menu {
  bottom: 3rem;
}

a {
  font-weight: bold;
  text-decoration: none;
}
main {
  width: 80vw;
  margin: 2rem auto;
  height: 100vh;
}
.carousel {
  position: relative;
}
.slides-container {
  width: 80vw;
  display: flex;
  flex-wrap: nowrap;
  overflow-y: hidden;
  overflow-x: hidden;
}
.slides-container .slide {
  flex-shrink: 0;
  width: 100%;
  text-align: center;
}
.prev {
  position: absolute;
  top: 45%;
  left: 0;
}
.next {
  position: absolute;
  top: 45%;
  right: 0;
}

.carousel-nav {
  margin: 10px auto;
  text-align: center;
  display: flex;
  justify-content: center;
}
.carousel-nav button {
  display: block;
  margin: 0;
  padding: 0;
  height: 20px;
  width: 20px;
  background-color: #a5a5a5;
  border-radius: 50%;
}
.carousel-nav button.active {
  background-color: cornflowerblue;
}
